:root {
  --Soft-red: hsl(7, 99%, 70%);
  --Soft-red-transparent: hsla(7, 99%, 70%, 0.5);
  --Yellow: hsl(51, 100%, 49%);
  --Yellow-transparent: hsla(51, 100%, 49%, 0.5);
  --graphic-design-text: hsl(167, 40%, 24%);
  --photography-text: hsl(198, 62%, 26%);
  --footer: hsl(168, 34%, 41%);
  --Very-dark-desaturated-blue: hsl(212, 27%, 19%);
  --Very-dark-grayish-blue: hsl(213, 9%, 39%);
  --Dark-grayish-blue: hsl(232, 10%, 55%);
  --Grayish-blue: hsl(210, 4%, 67%);
  --White-transparent: hsla(0, 0%, 100%, 0.282);
  --White: hsl(0, 0%, 100%);
}

:root {
  --font-family-1: "Barlow", sans-serif;
  --font-family-2: "Kavoon", cursive;
}

@media (prefers-reduced-motion: reduce) {
  * {
    animation: none;
    transition: none;
  }
}
*, *::before, *::after {
  margin: 0;
  padding: 0;
  border: 0;
  box-sizing: border-box;
  vertical-align: baseline;
}

img,
picture,
video,
iframe,
figure {
  max-width: 100%;
  width: 100%;
  display: block;
  object-fit: cover;
  object-position: center center;
}

html {
  scroll-behavior: smooth;
}

table, tr, td {
  border-collapse: collapse;
  border-spacing: 0;
}

svg {
  width: 100%;
  display: block;
  fill: currentColor;
}

body {
  max-width: 100%;
  min-height: 100vh;
  font-size: 100%;
  font-family: var(--font-family-1);
  color: var(--Very-dark-desaturated-blue);
  background: var(--White);
  line-height: 1.4em;
  font-smooth: always;
  --webkit-font-smoothing: antialiased;
  --moz-osx-font-smoothing: grayscale;
  transition: 0.5s ease-in-out;
}

h1, h2, h3 {
  margin-top: 0;
}

a {
  display: block;
}

p a {
  display: inline;
}

li {
  list-style-type: none;
}

h1, h2, h3, h4, h5, h6,
p, span, a, strong, blockquote,
i, b, u, em {
  font-size: 1em;
  font-weight: inherit;
  font-style: inherit;
  text-decoration: none;
  color: inherit;
}

blockquote::before,
blockquote::after,
q::before,
q::after {
  content: "";
  content: none;
}

form, input, textarea,
select, button, label {
  font-family: inherit;
  font-size: inherit;
  background-color: transparent;
  display: block;
  color: inherit;
}

.container {
  max-width: 90rem;
  padding: 0 1.5625rem;
  margin: 0 auto;
}
@media (min-width: 71.875rem) {
  .container {
    padding: 0;
  }
}

main {
  max-width: 90rem;
  margin: 0 auto;
}

.header {
  position: fixed;
  top: 0;
  left: 50%;
  transform: translateX(-50%);
  backdrop-filter: blur(1rem);
  width: 100%;
  display: flex;
  flex-flow: row wrap;
  justify-content: space-between;
  align-items: center;
  padding: 1.5rem;
  overflow: hidden;
  height: 4.5rem;
  transition: 0.5s ease-in-out;
  z-index: 1;
}
@media (min-width: 40rem) {
  .header {
    height: auto;
  }
}
.header__logo {
  order: 1;
}
.header__logo--img {
  width: auto;
  height: 1.6rem;
  transition: 0.5s ease-in-out;
}
@media (min-width: 71.875rem) {
  .header__logo--img {
    height: 2rem;
  }
}
.header__nav {
  order: 3;
  width: 100%;
}
@media (min-width: 40rem) {
  .header__nav {
    order: 2;
    width: auto;
    height: auto;
  }
}
.header__btn {
  order: 2;
  cursor: pointer;
}
@media (min-width: 40rem) {
  .header__btn {
    display: none;
  }
}
.header__btn--img {
  width: auto;
  height: 1.4rem;
}

.open {
  height: 24rem;
  pointer-events: auto;
}

.nav {
  position: relative;
  background-color: var(--White);
  margin-top: 3.5rem;
  border-top-left-radius: 0.5rem;
  border-bottom-left-radius: 0.5rem;
  border-bottom-right-radius: 0.5rem;
}
@media (min-width: 40rem) {
  .nav {
    background-color: transparent;
    font-size: 1.2rem;
    margin-top: 0;
  }
}
.nav::after {
  content: "";
  position: absolute;
  width: 0;
  height: 0;
  top: 0;
  right: 0;
  border-right: 1rem solid var(--White);
  border-top: 1rem solid transparent;
  border-left: 1rem solid transparent;
  border-bottom: 1rem solid var(--White);
  transform: translateY(-99%);
}
@media (min-width: 40rem) {
  .nav::after {
    display: none;
  }
}
.nav__ul {
  text-align: center;
  padding: 1rem 0;
}
@media (min-width: 40rem) {
  .nav__ul {
    display: flex;
    padding: 0;
  }
}
.nav__li {
  padding: 0.5rem 0;
}
@media (min-width: 40rem) {
  .nav__li {
    padding: 0;
  }
}
.nav__li:last-child a {
  color: var(--Very-dark-desaturated-blue);
  background-color: var(--Yellow);
  border-radius: 2rem;
  width: 40%;
  margin: 0 auto;
  transition: 0.4s ease-in-out;
}
@media (min-width: 40rem) {
  .nav__li:last-child a {
    width: 100%;
  }
}
.nav__li:last-child a:hover {
  color: var(--White);
  background-color: var(--White-transparent);
}
.nav__a {
  color: var(--Very-dark-grayish-blue);
  padding: 0.5rem 1rem;
}
@media (min-width: 40rem) {
  .nav__a {
    color: var(--White);
  }
}

.attribution {
  position: fixed;
  padding: 0 0 0 1.5rem;
  background: linear-gradient(var(--Very-dark-desaturated-blue) 0%, var(--photography-text) 100%);
  border-radius: 50px;
  bottom: 10px;
  right: 10px;
  width: 4rem;
  height: 4rem;
  overflow: hidden;
  transition: 0.5s ease-in-out;
  text-align: left;
  line-height: 1.2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 0.9rem;
}
.attribution__text {
  transition: 0.3s 0.3s;
  opacity: 0;
  min-width: 19rem;
  margin: 0.2rem 0 0;
  color: var(--White);
}
.attribution__text--a {
  color: var(--Soft-red);
}
.attribution__img {
  position: absolute;
  bottom: 0;
  right: 0;
  max-width: 4rem;
  border: 4px solid var(--Soft-red);
  border-radius: 50%;
  cursor: pointer;
  transition: 0.5s ease-in-out;
}
.attribution:hover {
  width: 19.5rem;
  border-radius: 50px;
}
.attribution:hover .attribution__text {
  opacity: 1;
}

.about {
  display: grid;
  grid-template-columns: 1fr;
  overflow: hidden;
}
@media (min-width: 40rem) {
  .about {
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr 1fr;
    grid-template-areas: "text1 img1" "img2 text2";
  }
}
@media (min-width: 40rem) {
  .about__picture--1 {
    grid-area: img1;
    transform: translateX(100%);
  }
  .about__picture--2 {
    transform: translateX(-100%);
    grid-area: img2;
  }
}
.about__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.about__post {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: flex-start;
  gap: 1.5rem;
  padding: 4rem 2rem;
}
@media (min-width: 71.875rem) {
  .about__post {
    padding: 8rem;
  }
  .about__post--1 {
    grid-area: text1;
  }
  .about__post--2 {
    grid-area: text2;
  }
}
.about__h2 {
  font-family: var(--font-family-2);
  font-size: 2rem;
  line-height: 2rem;
  transition: 0.5s ease-in-out;
}
@media (min-width: 71.875rem) {
  .about__h2 {
    font-size: 2.6rem;
    line-height: 2.6rem;
  }
}
.about__text {
  font-size: 1.1rem;
  line-height: 1.6rem;
  color: var(--Very-dark-grayish-blue);
  transition: 0.5s ease-in-out;
}
@media (min-width: 71.875rem) {
  .about__text {
    font-size: 1.2rem;
    line-height: 1.2rem;
  }
}
.about__btn {
  cursor: pointer;
  font-weight: 700;
  transition: 0.5s ease-in-out;
  position: relative;
}
.about__btn::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 0.375rem;
  background-color: var(--Yellow-transparent);
  bottom: -0.3rem;
  border-radius: 2rem;
  left: 0;
  transition: 0.3s ease-in-out;
  z-index: -1;
}
.about__btn:hover::after {
  background-color: var(--Yellow);
  bottom: -0.18rem;
}
@media (min-width: 71.875rem) {
  .about__btn {
    font-size: 1.2rem;
    line-height: 1.2rem;
  }
}
.about__btn--2::after {
  background-color: var(--Soft-red-transparent);
}
.about__btn--2:hover::after {
  background-color: var(--Soft-red);
}

.services {
  display: flex;
  flex-flow: column wrap;
}
@media (min-width: 71.875rem) {
  .services {
    flex-flow: row nowrap;
  }
}
.services__item {
  position: relative;
  max-height: 100vh;
  transform: translateX(-100%);
}
.services__item:last-child {
  transition-delay: 200ms;
}
.services__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}
.services__content {
  position: absolute;
  bottom: 0;
  padding: 0 2rem 3rem;
  text-align: center;
}
.services__content--1 {
  color: var(--graphic-design-text);
}
.services__content--2 {
  color: var(--photography-text);
}
@media (min-width: 40rem) {
  .services__content {
    width: 75%;
    left: 50%;
    transform: translateX(-50%);
  }
}
@media (min-width: 71.875rem) {
  .services__content {
    width: 60%;
  }
}
.services__h2 {
  font-family: var(--font-family-2);
  font-size: 2rem;
  line-height: 2rem;
  margin-bottom: 1rem;
}
.services__text {
  font-weight: 700;
  line-height: 1.8rem;
}

.testimonials {
  display: flex;
  flex-flow: column wrap;
  justify-content: center;
  align-items: center;
  min-height: 70vh;
  gap: 2rem;
  padding: 4rem 0;
}
.testimonials__heading {
  font-size: 1.5rem;
  font-family: var(--font-family-2);
  color: var(--Dark-grayish-blue);
}
.testimonials__container {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  gap: 0;
}

.card {
  max-width: 30rem;
  width: 100%;
  text-align: center;
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  padding: 3rem 5rem;
  color: var(---Very-dark-grayish-blue);
  transform: translate(-50%, -50%);
}
.card:nth-child(2) {
  transition-delay: 300ms;
}
.card:nth-child(3) {
  transition-delay: 600ms;
}
.card__img {
  width: 4rem;
  height: 4rem;
  border-radius: 50%;
}
.card__user {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  gap: 0.5rem;
}
.card__user--name {
  font-weight: 700;
  color: var(--Very-dark-desaturated-blue);
}
.card__user--work {
  font-size: 0.9rem;
  color: var(--Grayish-blue);
}

.contact {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  background-color: var(--footer);
  color: var(--graphic-design-text);
  padding: 3rem 0;
}
.contact__logo--svg {
  height: 2.1rem;
  width: 7.9rem;
  transform: scale(1.2);
  margin: 0 auto;
  text-align: center;
  fill: var(--graphic-design-text);
}
.contact__nav {
  width: 100%;
  max-width: 25rem;
}
.contact__ul {
  display: flex;
  flex-flow: row nowrap;
  justify-content: space-around;
  align-items: center;
  gap: 0;
}
.contact__a {
  font-size: 1.3rem;
  transition: 0.3s ease-in-out;
}
.contact__a:hover {
  color: var(--White);
}

.ssmm {
  display: flex;
  flex-flow: row nowrap;
  justify-content: center;
  align-items: center;
  gap: 1.5rem;
  width: 100%;
  height: 100%;
}
.ssmm__a:hover .ssmm__svg {
  fill: var(--White);
}
.ssmm__svg {
  width: 1.25rem;
  height: 1.25rem;
  transition: 0.4s ease-in-out;
}

.hero {
  position: relative;
  transform: translateY(-50%);
}
.hero__content {
  position: absolute;
  top: 0;
  transform: translateY(50%);
  width: 100%;
}
.hero__content--h1 {
  text-align: center;
  font-family: var(--font-family-2);
  color: var(--White);
  font-size: 4.5rem;
  line-height: 4.5rem;
  letter-spacing: 0.5rem;
  margin-bottom: 3rem;
}
.hero__content--arrow {
  width: 3rem;
  margin: 0 auto;
}

.projects {
  display: flex;
  flex-flow: row wrap;
  justify-content: center;
  align-items: center;
  gap: 0;
}
.projects__item {
  flex: 1 1 100%;
}
@media (min-width: 40rem) {
  .projects__item {
    flex: 1 1 50%;
  }
}
@media (min-width: 71.875rem) {
  .projects__item {
    flex: 1 1 25%;
  }
}

.hidden {
  opacity: 0;
  filter: blur(0.5rem);
  transition: 0.5s ease-in-out;
}

@media (preferences-reduced-motion) {
  .hidden {
    transition: none;
  }
}
.show {
  opacity: 1;
  filter: blur(0);
  transform: translate(0);
}